/* Full-screen background setup */
/* Slogan Container for stacking multiple announcements */
.slogan-container {
    position: absolute;
    top: 50px;
    right: 20px;
    max-width: 500px;
    display: flex;
    flex-direction: column;
    gap: 15px; /* Spacing between slogans */
  }
  .slogan-container p:last-of-type {
    color: white;
}
  
  
  .home-container {
    position: relative;
    width: 100%;
    height: 100vh; /* Full screen height */
    display: flex;
    align-items: center;
    justify-content: flex-start; /* Align slogan to the left */
    overflow: hidden;
  }
  
  /* Background Image */
  .background-image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url("../pictures/background.jpg"); /* Update path */
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    z-index: -1; /* Place image behind text */
  }
  
  /* News Announcement Styling */
  .slogan {
    font-size: 1.2rem;
    font-weight: bold;
    color: white;
    background-color: #333; /* Darker background */
    padding: 15px 20px;
    border-left: 5px solid #ffcc00; /* Yellow stripe for news effect */
    border-radius: 5px;
    text-align: left;
    font-family: "Arial", sans-serif;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  }
  /* News Link */
.slogan a {
    color: white;
    text-decoration: none;
    font-weight: bold;
  }
  
  .slogan a:hover {
    text-decoration: underline;
    color: #f28136; /* Highlight effect on hover */
  }
  
  /* Smaller text for the source and date */
  .news-source {
    font-size: 0.9rem;
    font-weight: normal;
    color: #ddd;
    margin-top: 5px;
    display: block;
  }